*{
    box-sizing: border-box;
}

.service-page{
    width: 100%;
    padding:10px;
    margin-top:10px;
    padding-top: 5px;
    display: flex;
    justify-content: center;
    overflow: hidden;
    
}
.content{
    width:50%;
    display: flex;
    flex-direction: column;
    box-shadow:0 0 .1rem .09rem rgba(0,0,0,.075);
    background-color: white;
    border-radius: 5px;
    margin-top: 40px;
    padding: 10px;
}
.content h1{
    margin-bottom: 15px;
}
.content p{
    padding: 10px;
}

@media screen and (max-width:768px) {
    .service-page{
        margin:0;
        padding:0.5rem;
    }
    .content{
        width:100%;
        padding:0.5rem;
        box-shadow: none;
    }
    
}
.content h1{
    text-align: center;
    padding: 20px;
}